/* 顶栏整体样式 */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #111;
  color: white;
  padding: 1rem;
  position: relative;
  flex-wrap: wrap;
}

/* 左侧 Logo */
.logo {
  font-size: 1.5rem;
}

/* ☰ 菜单按钮 */
.menu-button {
  background: none;
  color: white;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  display: none; /* 默认隐藏，手机再显示 */
}

/* 菜单链接容器 */
.nav-links {
  display: flex;
  gap: 1rem;
}

/* 链接样式 */
.nav-links a {
  color: white;
  text-decoration: none;
  padding: 0.5rem 0;
  border-bottom: 2px solid transparent;
}

.nav-links a:hover {
  border-color: white;
}

/* -------- 小屏幕：移动端样式 -------- */
@media (max-width: 768px) {
  .menu-button {
    display: block;
  }

  .nav-links {
    display: none;
    flex-direction: column;
    width: 100%;
    padding-top: 1rem;
  }

  .nav-links.open {
    display: flex;
  }
}